import React from 'react';
import { Link } from 'react-router-dom';

import './shop-item.scss';

function ShopItem({ id, name, imageUrl, price, onClickBuyBtn, countOfAdded }) {

    const onAddItem = () => {
        const obj = {
            id,
            name,
            imageUrl,
            price
        };
        onClickBuyBtn(obj);
    }

    return (
        <div className="shop-item">
            <div className="shop-item__img">
                <img src={imageUrl} alt="poster" />
            </div>
            <div className="shop-item__title">
                <Link to={`/shop/${id}`}>{name}</Link>
            </div>

            <div className="shop-item__bottom">
                <div className="row">
                    <div className="col-12 col-lg-4 shop-item__price">
                        {price} ₴
                    </div>
                    <div className="col-12 col-lg-8 text-right">
                        <button onClick={onAddItem} className="btn-add-to-cart">
                            +Додати {countOfAdded && <span>{countOfAdded}</span>}
                        </button>
                    </div>
                </div>
            </div>
        </div >
    )
}

export default ShopItem
