import axios from 'axios';

class ApiServices {
  _baseUrl = 'https://water-shop-api.herokuapp.com/';

  /**
   * 'http://localhost:3004/';
   * change _baseUrl on this url, if you run app with npm run dev and you want to use my fake json-server
   */

  getResource = async (url) => {
    const res = await fetch(`${this._baseUrl}${url}`)
    if (!res.ok) {
      throw new Error(`Could not fetch ${url}, received ${res.status}`);
    }
    const body = res.json();
    return body;
  }

  getGoods = async (sortBy, category, sortOrder) => {
    const { data } = await axios.get(`${this._baseUrl}shopItems`, {
      params: {
        category: category,
        _sort: sortBy,
        _order: sortOrder
      }
    })
    return data;
  }

  getPopularGoods = async (catId) => {
    const { data } = await axios.get(`${this._baseUrl}shopItems`, {
      params: {
        category: catId,
        _sort: 'rating',
        _order: 'desc',
        _start: 0,
        _end: 4
      }
    })
    return data;
  }

  getShopItemById = async (id) => {
    const { data } = await axios.get(`${this._baseUrl}shopItems`, {
      params: {
        id: id
      }
    })
    return data;
  }

  getShopCategories = async () => {
    const items = await this.getResource(`shopCategories`);
    return items;
  }

  getAdvantages = async () => {
    const items = await this.getResource(`advantages`);
    return items;
  }

}

const apiServices = new ApiServices();

export default apiServices;

