import React from 'react'
import { Container } from '../../components';

import './payment-delivery-page.scss';

export default function ContactsPage() {
    return (
        <Container title="Оплата і доставка">

            <b>ДЛЯ ЗАМОВЛЕННЯ  ДОСТАВКИ  ВОДИ  ТА СУПУТНІХ ТОВАРІВ ВАМ НЕОБХІДНО:</b><br />
            зателефонувати або надіслати СМС за номерами (068)-655-32-05, (095)-015-12-22, (063)-015-12-22 або здійснити замовлення на нашому сайті;
            вказати адресу доставки замовлення;
            узгодити час доставки (з інтервалом не менше 2-ох годин) по телефону або вказати його в інтернет-замовленні.
            <br /><br />
            <b>ЧАС ТА УМОВИ ДОСТАВКИ:</b><br />
            Замовлення на доставку води  виконуються в той сами день (при оформленні до 11 години).
            Замовлення на доставку супутніх товарів приймаються до 17 год. та виконуються на наступний день.
            Мінімальне замовлення товарів без бутильованої води – 200 гривень.<br />
            Доставка по м.Львів - безкоштовна!
            <br /><br />
            <b>ОПЛАТА:</b>
            <br />
            При замовлені товарів  можна провести оплату коштів у зручний для Вас спосіб:

            – готівкою при доставці;

            – переказ коштів на банківську карту;
        </Container>
    )
}
