import React from 'react';
import { Container } from '../../components';
import PreloadImage from 'react-preload-image';

import './about-us-page.scss';
import Image from '../../assets/images/about-us.png';

const AboutUsPage = () => {

    const image = <PreloadImage className="aboutus__preloader-img" src={Image} lazy />

    const text = <p className="m-4">
        Наша "Кришталева" найсмачніша, очищена вода із збалансованим складом. <br />
        Ідеальна для приготування їжію <br />
        Підходить для використання в кавових машинах та інших обладнаннях.<br />
        Не залишає накипу.<br />
        Має невисокий рівень мінералізації.<br />
        Найчастіше її замовляють додому, в офіси, ресторани та медичні заклади.<br />
        <br />
        Також пропонуємо:<br />
        <br />
        - бутлі, корки;<br />
        - помпи;<br />
        - кулера;<br />
        - паперові та пластикові стаканчики;<br />
        - гігієнічні та вологі серветки;<br />
        - теалетний папір;<br />
        - маски медичні;<br />
    </p>

    return (
        <Container title="Про нас!">
            <div className="row">
                <div className="col-md-6 text-center">{image}</div>
                <div className="col-md-6 about-us-text">{text}</div>
            </div>
        </Container>
    )
};

export default AboutUsPage;
